html {
  height: 100%;
  scroll-behavior: smooth; }

body {
  display: grid;
  min-height: 100%;
  margin: 0;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  font-family: "Poppins", "Arial", sans-serif;
  font-weight: 500;
  font-size: 16px;
  line-height: 24px;
  color: #1D1613; }

.page-header {
  padding-top: 30px; }
  @media (max-width: 1023px) {
