        fill: #ffffff; }
      .page-header--opened .page-header__toggle {
        background-color: transparent; }
        .page-header--opened .page-header__toggle::before, .page-header--opened .page-header__toggle::after {
          width: 23px;
          top: 22px;
          left: 13px;
          background-color: #ffffff; }
        .page-header--opened .page-header__toggle::after {
          content: "";
          position: absolute;
          height: 2px;
          transform: rotate(-45deg); }
        .page-header--opened .page-header__toggle::before {
          transform: rotate(45deg); } }

.page-header__top {
  display: flex;
  margin-bottom: 37px;
  font-size: 14px;
  line-height: 21px; }
  @media (max-width: 1023px) {
