  color: #9B9997; }

.page-header__top form {
  display: flex;
  align-self: center;
  margin-right: 345px;
  padding-bottom: 19px; }
  @media (max-width: 1023px) {
    .page-header__top form {
      order: 4; } }

.page-header__user {
  padding-bottom: 22px;
  align-self: center; }

.page-header__user a {
  color: #1D1613; }

.page-header__logo {
  margin-right: 429px; }
  @media (max-width: 1023px) {
    .page-header__logo {
      margin: 0 auto; } }

.page-header__logo svg {
  fill: #a87b62; }

.page-header__login {
  margin-right: 35px; }
  @media (max-width: 1023px) {
    .page-header__login {
      display: none; } }

@media (max-width: 1023px) {
  .page-header__cart {
    position: relative;
    font-size: 0; }
    .page-header__cart::after {
      position: relative;
      content: url("../img/icon-cart.svg");
      width: 18px;
      height: 16px; } }

.page-header__nav {
  padding-bottom: 19px; }

.page-header__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0 auto;
  max-width: 493px; }

.page-header__item a {
  color: #1D1613; }

.page-header__item:not(:last-child) {
  margin-right: 35px; }

.page-header__bottom {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 195px;
  padding-bottom: 194px;
  background-image: url("../img/header-main-desktop@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  font-family: "Cormorant Garamond", "Times", serif;
  color: #ffffff; }
  @media (min-resolution: 2dppx) {
    .page-header__bottom {
      background-image: url("../img/header-main-desktop@2x.jpg"); } }

.page-header__bottom h2 {
  margin: 0;
  margin-bottom: 26px;
  font-size: 45px;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.03em; }

.page-header__bottom p {
  margin: 0;
  font-size: 38px;
  font-weight: 400;
  font-style: italic;
  text-transform: lowercase; }

.page-header__toggle {
  display: none; }
  @media (max-width: 1023px) {
    .page-header__toggle {
      display: block; } }
  @media (max-width: 1023px) and (max-width: 1023px) {
    .page-header__toggle {
      display: block;
      position: relative;
      top: 0;
      right: 0;
      width: 50px;
      height: 50px;
      border: none;
      background-color: transparent; }
      .page-header__toggle::before {
        content: "";
        position: absolute;
        top: 19px;
        left: 14px;
        width: 22px;
        height: 2px;
        background-color: #1D1613;
        transition-property: transform, box-shadow, top;
        transition-duration: 0.3s; } }
